import { AxiosResponse } from "axios"
import { generatePath } from "react-router-dom"
import { axiosClient } from "@rp/react/axios-client"
import { BriefConfig, BriefConfigAPI, BriefConfigGetAllResponse } from "../brief.types"
import { BRIEF_API_PATH, BRIEF_BY_ID_API_PATH } from "./briefConfigs.routes"

const createBrief = async (briefPost: BriefConfigAPI): Promise<BriefConfig> => {
  const { data } = await axiosClient.post<BriefConfigAPI, AxiosResponse<BriefConfig>>(
    BRIEF_API_PATH,
    briefPost,
  )
  return data
}

const loadAllBriefs = async (): Promise<Array<BriefConfig>> => {
  const { data } = await axiosClient.get<BriefConfigGetAllResponse>(BRIEF_API_PATH)
  return data.results
}

const loadBriefConfigById = async (briefId: string | undefined): Promise<BriefConfig | null> => {
  if (!briefId) return null

  const { data } = await axiosClient.get<BriefConfig>(
    generatePath(BRIEF_BY_ID_API_PATH, { briefId }),
  )

  return data
}

const deleteBriefConfigById = async (briefId: string | undefined): Promise<void> => {
  if (!briefId) return

  await axiosClient.delete<BriefConfig>(generatePath(BRIEF_BY_ID_API_PATH, { briefId }))
}

const editBriefConfig = async (
  briefEdit: BriefConfigAPI,
  briefId: string | undefined,
): Promise<void> => {
  if (!briefId) return

  await axiosClient.put<BriefConfig>(
    generatePath(BRIEF_BY_ID_API_PATH.replace(":briefId", briefId)),
    briefEdit,
  )
}

export { createBrief, loadAllBriefs, loadBriefConfigById, deleteBriefConfigById, editBriefConfig }
